<script>
  import appStore,{selectedStore} from "./../../store";
  import BlockDetail from "./BlockDetail.svelte"
  export let hash;
  export let open;

  function close(params) {
     open =false;
     document.getElementById('id01').style.display='none'
  }
</script>

<div id="id01" class="w3-modal" on:click={close}>
  <div class="w3-modal-content w3-card-4">
    <header class="w3-container w3-teal">
      <span
       on:click={close}
        class="w3-button w3-display-topright">
        &times;
      </span>
      <h6>{hash}</h6>
    </header>
    <div class="w3-container">

      {#if $selectedStore}
       <BlockDetail data={$selectedStore}/>
      {:else}
        <div>Loading...</div>
      {/if}




    </div>
  </div>
</div>
